@import url('https://webfontworld.github.io/goodchoice/Jalnan.css');
body{
  /* background-color: rgb(106, 0, 255); */
  background: url(/public/images/bg.png);
  font-family: 'Jalnan';
  color:#464646;
  font-weight: 900;
}
.ClipLoader{
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.WeatherWrap{
  width: 60%;
  padding: 2rem;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.App{
  
}
.weatherbox{
  background-color: rgba(140, 160, 169, 0.34);
  border-radius: 20px;
  height: 350px;
  line-height: 65px;
  color: #fff;
  font-size: 1.5rem;
}
.weatherbox_wrap{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.box_left{
  margin-top: 40px;
}
.WeatherButton{
  gap: 0.3rem;  
  text-align: center;
}
.WeatherButton>div{
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
}
button{
  border: none;
  padding: 1.5rem 2.5rem;
  border-radius: 10px;
  cursor: pointer;
  color: #fff; 
  font-family: 'Jalnan';
  font-size: 1rem;
  background-color: rgba(140, 160, 169, 0.7);
  transition: 0.25s;
}
button:hover{
  background-color: #fff;
  color: rgba(140, 160, 169, 0.7);
}
.city{
  height: auto;
  margin-left: 10px;
}
@media screen and (max-width:768px){
  .weatherbox_wrap{
    width: 100%;
    padding: 0rem;
    position: absolute;
    justify-content: center;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}
  .weatherbox{
    background-color: rgba(140, 160, 169, 0.34);
    border-radius: 20px;
    float: left;
    position: relative;
    right: 60px;
    height: 350px;
    line-height: 65px;
    color: #fff;
    font-size: 1.3rem;
    width: 100%;
  }
  img{
    width: 70%;
  }
  .weatherbutton{
    width: 100%;
  }
  .WeatherButton{
    display: flex;
    
  }
  .WeatherButton>div{
    flex-direction: column;
    align-items: center;
    margin-top: 0px;
  }
  button{
    padding: 1.5rem 1.8rem;
    font-size: 0.7rem;
    transition: 0.25s;
    width: 100%;
  }
  .city{
    width: 100%;
    margin-top: 10px;
    margin-right: 10px;
  }
}